import { useState } from "react";
import "./styles.css";

export default function App() {
  let [clickedItems, setClickedItems] = useState([]);
  const [boxClick, setClick] = useState(1);
  const matrix = [
    {
      id: 1
    },
    {
      id: 2
    },
    {
      id: 3
    },
    {
      id: 4
    }
  ];

  const handleClick = (e, id) => {
    document.getElementById(e.target.id).innerHTML = `box # ${boxClick}`;
    document.getElementById(e.target.id).className = "buttonBackground";
    setClick(boxClick + 1);

    clickedItems.push(e.target.id);
    setClickedItems(clickedItems);

    if (clickedItems.length > 2) {
      for (let i = 0; i < clickedItems.length - 2; i++) {
        document.getElementById(clickedItems[i]).className =
          "buttonBackgroundRevert";
      }
    }
  };

  return (
    <div className="App">
      <table>
        {matrix.map(function (id, index) {
          return (
            <tr key={index}>
              <td key={`${index}_0`}>
                <button
                  id={`${index}_0`}
                  onClick={(e, id) => handleClick(e, id)}
                ></button>
              </td>
              <td key={`${index}_1`}>
                <button
                  id={`${index}_1`}
                  onClick={(e, id) => handleClick(e, id)}
                ></button>
              </td>
              <td key={`${index}_2`}>
                <button
                  id={`${index}_2`}
                  onClick={(e, id) => handleClick(e, id)}
                ></button>
              </td>
              <td key={`${index}_3`}>
                <button
                  id={`${index}_3`}
                  onClick={(e, id) => handleClick(e, id)}
                ></button>
              </td>
            </tr>
          );
        })}
      </table>
    </div>
  );
}
